.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 70%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 20px;
  left: 15%;
  top: 12%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  font-family: inherit;
  overflow: auto;
}

@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}

@media (max-width: 250px) {
  .Modal {
    top: 10%;
    padding: 5px;
  }
}
